import React, {useState} from 'react';
import {Draggable} from "react-beautiful-dnd";
import styled from 'styled-components';
import {ParagraphForm} from "./ParagraphForm";
import {DrupalContext} from "../WidgetManager";
import {AdminTitleField} from "./Widgets/AdminTitleField";
import {DrupalModal} from "./Atoms/DrupalModal";
import {DrupalModalFooter} from "./Atoms/DrupalModalFooter";
import {DropButtons} from "./DropButtons"
import {ConfirmDialog} from "./Atoms/ConfirmDialog";

const ParagraphWrapper = styled.div`
  margin: 5px;
  border: 1px solid #BDBDBD;
  width: 0;
  height: 80px;
  line-height: 80px;
  padding: 0px 85px 0px 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  position: relative;
  flex: ${props => props.itemWidth} 1 0;
  background: ${props => props.isDragging ? '#dafcdf' : '#fff'};

  .img-helper {
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }

  img {
    vertical-align: middle;
  }

  .dropbutton-wrapper {
    position: absolute;
    top: calc(50% - 13px);
    right: 5px;
  }
`;

const ItemIcon = styled.img`
  max-height: 35px;
  max-width: 35px;
  margin-right: 30px;
`;


export const Paragraph = ({item, ...props}) => {

  let submitButton = React.createRef();
  const [modalOpen, setModalOpen] = useState(Object.keys(item.entity).length == 1);
  const [deleteConfirmOpen, setDeleteConfirmOpen] = useState(false);

  const getItemIcon = (availableTools) => {
    let iconSrc = 'data:image/png;base64,';
    if (availableTools[item.entity.type[0].target_id].icon) {
      iconSrc = availableTools[item.entity.type[0].target_id].icon;
    }

    return <ItemIcon
      className="item-icon"
      role="presentation"
      alt=""
      src={iconSrc}
    />;
  };

  /**
   * Form submit handler.
   */
  const onFormSubmit = (e) => {
    e.preventDefault();
    setModalOpen(!modalOpen);
  };

  /**
   * Modal close handler will instead click the form submit button.
   */
  const onRequestClose = () => {
    submitButton.current.click();
  };

  return (
    <Draggable
      draggableId={props.id}
      index={props.index}
      isDragDisabled={!props.isDraggable}
    >
      {(provided, snapshot) =>
        <DrupalContext.Consumer>
          {drupalContext =>
            <ParagraphWrapper
              ref={provided.innerRef}
              {...provided.draggableProps}
              {...provided.dragHandleProps}
              isDragging={snapshot.isDragging}
              id={props.id}
              itemWidth={item.width}
            >
              <span className="img-helper"></span>
              {getItemIcon(drupalContext.tools)}
              {item.admin_title}

              <DropButtons
                buttons={[
                  <button
                    type="button"
                    className="button"
                    onClick={() => setModalOpen(!modalOpen)}
                  >
                    Edit<span
                    className="visually-hidden">{item.admin_title}</span>
                  </button>,
                  <button
                    type="button"
                    className="button"
                    onClick={() => setDeleteConfirmOpen(true)}
                  >
                    Delete
                  </button>,
                ]}
              />

              <DrupalModal
                isOpen={modalOpen}
                onRequestClose={onRequestClose}
                contentLabel={`Edit ${props.typeLabel} > "${item.admin_title}"`}
                wrapperProps={{style: {height: "calc(100% - 109px)"}}}
              >
                <form onSubmit={onFormSubmit}>
                  <div style={{padding: "20px"}}>
                    <AdminTitleField
                      textField={modalOpen}
                      item={item}
                      onChange={drupalContext.onAdminTitleChange.bind(undefined, item.id)}
                    />
                    <ParagraphForm
                      apiUrls={drupalContext.apiUrls}
                      item={item}
                    />
                  </div>
                  <DrupalModalFooter>
                    <input
                      type="submit"
                      className="button button--primary"
                      value="Continue"
                      ref={submitButton}
                    />

                  </DrupalModalFooter>
                </form>
              </DrupalModal>

              <ConfirmDialog
                open={deleteConfirmOpen}
                title="Delete this item?"
                dialog="This action can not be undone."
                onCancel={() => setDeleteConfirmOpen(false)}
                onConfirm={() => {
                  setDeleteConfirmOpen(false);
                  drupalContext.removeParagraph(props.id)
                }}
              />

            </ParagraphWrapper>
          }
        </DrupalContext.Consumer>
      }
    </Draggable>
  )
};
